import axios, { AxiosResponse } from "axios";
import Clock from "./70-clock.vue";
import WorldMap from "./70-world-map.vue";
import { Vue, Component } from "vue-property-decorator";
import {
  collection,
  Firestore,
  getDocs,
  doc,
  QueryDocumentSnapshot,
  QuerySnapshot,
  getDoc,
  DocumentSnapshot,
  query,
  where,
  orderBy,
  limit,
  onSnapshot,
  deleteDoc,
} from "firebase/firestore";
import {
  getAuth,
  onAuthStateChanged,
  User,
  Auth,
  signOut,
  deleteUser,
} from "firebase/auth";
import {firebaseConfig} from "@/myconfig";
import {initializeApp, FirebaseApp} from "firebase/app";
import { getFirestore, setDoc } from 'firebase/firestore';
import WorldClock from './70-clock.vue';

const timezoneDBUrl = "http://api.timezonedb.com/v2.1";
type City = {
  name: string;
  timeZone: string;
};

type TimeZoneData = {
  countryName: string;
  gmtOffset: number;
  regionName: string;
  zoneName: string;
};
@Component({ components: { Clock, WorldMap, WorldClock } })
export default class Sample extends Vue {
  geoPos: { lat?: number; lng?: number } = {};
  selectedCities: Array<City> = [];
  apiKey = "";
  auth: Auth | null = null;
  name_zone ="";
  selectedData: Array<City> = [];
  auth_backup : Auth | null = null;
  
  
  
  mounted(): void {

    this.apiKey = process.env.VUE_APP_TIMEZONE_API_KEY;
    this.readData();
  
  }

  readData(): void{
this.auth = getAuth();
//this.auth_backup = this.auth;
 //var uid = this.auth.currentUser!.uid;
 this.auth.onAuthStateChanged((user)=>{
if (user != null){
 const uid = user.uid;

var myapp: FirebaseApp = initializeApp(firebaseConfig);
    var db:Firestore = getFirestore(myapp);
 
 const myZone = collection (db, uid);
 getDocs(myZone).then((qs: QuerySnapshot) => {
    qs.forEach((qd: QueryDocumentSnapshot) => {
      console.log("From Firestore: ", qd.id, qd.data());
      const details = qd.data();
    this.selectedCities.push({ name: details.name, timeZone: details.timeZone });
       });
    });


}
 });
 


  }


  get geoPosition(): string {
    if (this.geoPos.lat && this.geoPos.lng)
      return `${this.geoPos.lat.toPrecision(5)},${this.geoPos.lng.toPrecision(
        5
      )}`;
    else return "N/A";
  }

  searchCity(): void {
    const param = new URLSearchParams();
    param.append("key", this.apiKey);
    param.append("format", "json");
    param.append("by", "position");
    param.append("lat", this.geoPos.lat!.toString());
    param.append("lng", this.geoPos.lng!.toString());
    const tzUrl = `${timezoneDBUrl}/get-time-zone?` + param.toString();
    // Use a Web Proxy Server to get around CORS issue
    // since timezonedb.com does not allow CORS
    axios
      .request({
        method: "GET",
        url: "https://api.allorigins.win/get",
        params: {
          url: tzUrl,
        },
      })
      .then((r: AxiosResponse) => {
        return r.data;
      })
      .then((r: any) => JSON.parse(r.contents))
      .then((r: TimeZoneData) => {
        // Add the selected location to our array if the region name not yet in the array

        var searchRegion = this.selectedCities.find((x) =>{return x.name == r.regionName});
        if (searchRegion == undefined){

        this.selectedCities.push({ name: r.regionName, timeZone: r.zoneName });
        }
        console.log(r.regionName);
        console.log(this.selectedCities);
      });
  }

// save data of the user
SaveData(): void{
this.auth = getAuth();
var uid = this.auth.currentUser!.uid;

var myapp: FirebaseApp = initializeApp(firebaseConfig);
var db:Firestore = getFirestore(myapp);

// add a new document with our own uid
this.selectedCities.forEach(
    async (z: { name: string; timeZone: string }) => {
      var d = doc(db, uid, z.name );
      await setDoc(d, { name: z.name, timeZone: z.timeZone });
    }
  );

}

// delete user data
DeleteData(): void{
this.auth = getAuth();
var uid = this.auth.currentUser!.uid;

var myapp: FirebaseApp = initializeApp(firebaseConfig);
var db:Firestore = getFirestore(myapp);

const illi = doc(db, uid, this.name_zone);
  deleteDoc(illi).then(() => {
    console.log("zone deleted");
    this.selectedCities.splice(0, this.selectedCities.length);
    this.readData();

  });

}

DeleteAcc(): void {
    this.auth = getAuth();
   
    this.auth.onAuthStateChanged((user)=>{
   if (user != null){
    const uid = user.uid;
 
    deleteUser(user!)
      .then(() => {

        if (this.auth) signOut(this.auth);
    // Back to the previous page
    this.$router.back();
    
        
      });
      
  }
    });
    }


  what(geoPos: { lat: number; lng: number }): void {
    // When the user pans the map left/right the longitude
    // angle can be out of the [-180,+180] range
    while (geoPos.lng > 180) geoPos.lng -= 360;
    while (geoPos.lng < -180) geoPos.lng += 360;
    this.geoPos = { ...geoPos };
  }
}
