












import { Component, Prop, Vue } from "vue-property-decorator";
import {
  getAuth,
  onAuthStateChanged,
  User,
  Auth,
  signOut,
  deleteUser,
} from "firebase/auth";
import Sample from "./70-world-time.vue";




@Component({ components: { Sample } })
export default class HomeView extends Vue {
  @Prop() readonly byWayOf!: string;
  VUE_APP_TIMEZONE_API_KEY = "G19SEJFBNYPO";
  userPhotoURL = "";
  auth: Auth | null = null;
  userInfo = "Thanh Tran";
  message = "";

  mounted(): void {
    this.auth = getAuth();
    onAuthStateChanged(this.auth, (user: User | null) => {
      console.log("Auth changed", user);
      if (user) {
        this.userPhotoURL = user.photoURL ?? "";
        this.userInfo = `${user.displayName ?? "Thanh Tran"}`;
      }
    });
  }

  showMessage(txt: string) {
    this.message = txt;
    // The message will automatically disappear after 5 seconds
    setTimeout(() => {
      this.message = "";
    }, 5000);
  }

  

// this method to log out
  outtahere(): void {
    if (this.auth) signOut(this.auth);

    // Back to the previous page
    this.$router.back();
  }
}
